import React from 'react';
import { HashRouter as Router, Route, Routes, Link } from 'react-router-dom';

const DrivenPiles = () => <h2>Driven Piles</h2>;
const BoredPiles = () => <h2>Bored / CFA Piles</h2>;
const ScrewPiles = () => <h2>Screw Piles</h2>;

const App = () => {
  return (
    <Router basename="/uwa-pile-capacity-calculator">
      <div className="App">
        <header>
          <h1>UWA Pile Capacity Calculator</h1>
          <h2>Axial Capacity Calculator using CPT</h2>
        </header>

        <nav>
          <Link to="/driven"><button>Driven Piles</button></Link>
          <Link to="/bored"><button>Bored / CFA Piles</button></Link>
          <Link to="/screw"><button>Screw Piles</button></Link>
        </nav>

        <main>
          <Routes>
            <Route path="/driven" element={<DrivenPiles />} />
            <Route path="/bored" element={<BoredPiles />} />
            <Route path="/screw" element={<ScrewPiles />} />
            <Route path="/" element={<h2>Welcome! Select a pile type.</h2>} />
          </Routes>
        </main>
      </div>
    </Router>
  );
};

export default App;